/*-- Coded Prototype Framework Styles --*/
.cpf--header {
  display: flex;
  padding: 2rem;
  font-family: "TradeGothicWeb";
  /* -Demo Old Version----------------------- */
  /* -Demo New Version----------------------- */
  background-color: #3f7dc7;
  border-bottom: 1px solid #32649f;
  color: #ffffff; }

.cpf--header h2 {
  font-size: 22px;
  font-weight: bold;
  padding: 0px;
  margin: 0px; }

/*-- CPF Navigation --*/
.cpf-navbar {
  display: flex;
  padding: 2rem;
  background-color: #fafafa;
  border-bottom: 1px solid #f1f1f1; }

.cpf-navbar ul {
  padding: 0px;
  margin: 0px; }

.cpf-navbar ul li {
  list-style: none;
  display: inline-block;
  padding: 0rem;
  padding-right: 2rem; }

.cpf-navbar ul li a {
  text-decoration: none;
  font-family: "TradeGothicWeb";
  transition: all 0.1s ease-in-out;
  /* -Demo Old Version----------------------- */
  /* -Demo New Version----------------------- */
  color: #3f7dc7; }

.cpf-navbar ul li a:hover {
  text-decoration: underline; }

.cpf-navbar ul li.active a {
  text-decoration: underline;
  font-weight: bold; }

/*-- CPF Content Container --*/
.cpf-content-container {
  display: flex;
  flex-wrap: wrap;
  padding: 2rem; }

.cpf-content-container h3 {
  width: 100%;
  float: left;
  clear: both; }

.cpf-content-container table {
  table-layout: fixed;
  width: 100%;
  border-spacing: 0px;
  border: 1px solid #e1e1e1;
  font-family: "TradeGothicWeb";
  color: #555555; }

.cpf-content-container table td {
